import React from "react"; 


 function BurgerMenu (props){
 

    
        return(
            <div className="bur" style={{paddingTop:'10px', paddingBottom:'10px',position: 'fixed',zIndex:'2',width: '100vw',display: 'flex', backdropFilter: 'blur(14px)',backgroundColor:'#25418C',borderBottom:'solid #fff 1px'}} > 
            <div onClick={props.handleClick}  className={`hamburger ${props.clicked ? 'is-active':''} `} id="hamburger-6">
            <span className="line"></span>
                <span className="line"></span>
                <span className="line"></span>
            </div>
            
            </div>
        )
    }

export default BurgerMenu