import React,{useState} from "react";
import '../style2.css';
import logos from '../logo.png'
import Porque from './porque'

function Fondo1 () {




        const [clicked, setClicked] = useState(false)
        const handleClick= ()=>
    {
        setClicked(!clicked)
    }
        return(
        <div className="fondo"  id="porque1" >
            
    
            <div className="fondobis">
            <img  className='Logo'src={logos} alt="logos"/>
            
            <h1 style={{paddingTop:'45px',maxWidth:'700px'}}> Cooperacción tiene  como propósito promover los valores del cooperativismo 
                en la comunidad escolar, dar a conocer sus principales características, 
            sus principios y su historia.</h1>
            </div>
             
            {/* <h3 className="title"   > POR QUÉ Y PARA QUÉ </h3> */}
       
            <Porque clicked={clicked} setClicked={setClicked} />    
            
         </div>
        
        )
    }



export default Fondo1