import React from "react";
import nodos from '../nodos_1.png'
import facebook from '../facebook.png'
import youtube from '../youtube.png'
import linkedin from '../linkedin.png'
import instagram from '../instagram.png'

export default function footer (){
    return(
    <div id="footer" style={{maxWidth:'550px',alignSelf:'center',width:'90%',borderTop:'solid 2px rgb(245 241 245 / 35%)'}} >
        <img src={nodos}  style={{paddingRight:'3%'}} /> 

        <div className="socialMedia" style={{display:'flex'}}>
                        <div ><a  href="https://www.youtube.com/channel/UCok2neEAeAvAkQ6pFb3O4DA"><img src={youtube} /></a></div>
                        <div ><a  href="https://ar.linkedin.com/company/fundacion-nodos"><img src={linkedin} /></a></div>
                        <div ><a  href="https://www.facebook.com/fundacionnodos/posts"><img src={facebook} /></a></div>
                        <div ><a  href="https://www.instagram.com/"><img src={instagram} /></a></div>
                        
                    </div>

                    
    </div>


    )
}
