import React, { useState } from "react";
import { motion } from "framer-motion";




 function Fondo2Article (props){
  
  const inactive = {opacity: 0,y:0}
  const active = {opacity: 1,y:0}
    const [clicked,setClicked] = useState (null)
   
    const x = {opacity:1, y : 0,
    }
  const y = {opacity:0,y: 0, 
    
  }
  const [clicked1,setClicked1] = useState(true)
  const handleClick1 = ()=>
      {
          setClicked1(!clicked1)
      }

    const handleClick =(i)=>{
        if (clicked === i){
            return setClicked(null)
        }
        setClicked (i)
    }

    
    
    const array = [
      {
          title:'Objetivo',
          text: <p>
            Cooper<b>acción</b> es un juego de escape.
            El objetivo es resolver un problema juntos a través de cumplir 4 misiones. Se parte de una situación que se comparte con el grupo y se los desafía a resolverla en un tiempo determinado de tiempo.
       
          </p>
          
      },{
        title:'Participantes',
        text:<p>
          Los participantes formarán 4 participantes, idealmente cada grupo 
    debe tener entre 3 y 6 personas (si son menos o más se puede
    jugar también, pero es un requisito que siempre haya 4
    grupos).
    <br/>
    
        </p>
      },{
        title:'Inicio',
        text: <div>
           <p>
              Para comenzar, se tomará la <b>Tarjeta de Inicio.</b>
              <br/>
              La misma contiene la historia central del juego.
              <br/>
              Puede leerse o reproducir el audio ( a través de escanear el
              código QR) con su <i animate={clicked ? active : inactive} style={{cursor:'pointer',textDecoration:'underline', color: '#f1f5f1'}}  onClick={handleClick1}>narración.</i>
              </p>
              <motion.p style={{fontSize:'17px',fontStyle:'italic'}} animate={clicked1 ? inactive : active} className={clicked1 ? 'inactive' : 'inactive1'}> 
              
              <br/>
              Es viernes por la tarde, los chicos y las chicas de 1°, 2°, 3° y 4° año son los únicos cursos que
              quedan en la escuela. Están a punto de finalizar su día de clase, guardando las carpetas en sus
              mochilas, cuando algo misterioso sucede: todas las ventanas y puertas de la escuela se cierran
              de golpe. Docentes y directivos desaparecen. Solo las y los estudiantes quedan adentro. Nadie
              puede salir. No hay forma de comunicarse con el exterior. De repente una voz se hace
              escuchar. Anuncia que la única forma de poder salir de la escuela y no quedarse allí todo el fin
              de semana es encontrando la contraseña de 4 dígitos que destrabará todas las salidas.
              
              <br></br>
              <br/>
              </motion.p>
              <p> Una vez que se ha leído el texto, se explica la dinámica a los
              participantes.
              </p>
        </div>
      },{
        title:'Dinámica',
        text: <div>
             <p>El objetivo es resolver las 4 Misiones para poder salir. <b>Solo podrán hacerlo cooperativamente.</b></p>
              <br/>
              <ul>
                  <li>
                  Cada equipo tendrá una consigna diferente
                  </li>
                  <br/>
                  <li>
                  Solo se puede pasar a la Misión siguiente si los 4 equipos resolvieron su consigna
                  </li>
                  <br/>
                  <li>
                  Cuando la resuelvan, deberán validar con el docente que este
                  correcta y este le entregará una pieza de rompecabeza.
                  </li>
                  <br/>
                  <li>Con las piezas de rompecabeza de los 4 grupos se formará una pregunta. La respuesta a esa pregunta les permitirá acceder a los dígitos de la contraseña que necesitan para salir. 
                  </li>
                  <li>Al terminar la Misión 4 tendrán los 4 dígitos para abrir el candado y finalmente salir.
                  </li>
              </ul>
    
    
    
    
              <br/>
              <p>Deberán realizar todo en 50 minutos. 
              <br/>
              <br/>
              Al entregar las tarjetas de la Misión 1, se coloca el reloj y se invita a los equipos a comenzar el camino que los llevará a la “la salida”.
              </p>  
        </div>
      },{
        title:'Ayudas',
        text:<p>
          Durante el juego podrán pedir hasta <b>2 Tarjetas de ayuda </b> cuando lo deseen. 
                  Pero deben acordar entre todos los equipos cuando lo piden. Y la ayuda se brinda a todos.
        </p>
      },{
        title:'El candado',
        text: <div>
          
          <p> Cuando los 4 grupos completan una misión pueden armar el 
                  rompecabezas que permitirá leer una pregunta cuya respuesta 
                  siempre es un número 
                  <br/>
                  <b> Las 4 preguntas con sus respuestas y el número de la 
                      contraseña. </b> 
                  <br/>
                  <br/>
                  </p>
                  <ul>
                      <li><b>Misión 1</b><br/> Pregunta:  ¿Cuál es el último dígito del año de la 
                  creación de la bandera Argentina? (1812) <br/>Dígito: 2 </li>
                      <br/>
                      <br/>
                      <li><b>Misión 2</b> <br/> Pregunta: ¿Cuántos senadores se eligen por 
                      provincia en nuestro país? <br/>Dígito: 3 </li>
                      <br/>
                      <br/>
                      <li><b>Misión 3</b> <br/> Pregunta: ¿Cuál es la raíz cuadrada de 81? <br/>Dígito: 9 </li>
                      <br/>
                      <br/>
                      <li><b>Misión 4</b><br/> Pregunta: ¿Cuántas provincias argentinas comienzan 
                      con una vocal? <br/>Dígito: 1 </li>
                      <br/>
                      <br/>
                  </ul>
                    <p>  Asi, la contraseña para abrir el candado es <b> 2391 </b> </p>
                    <br/>
        </div>
      },{
        title:'El premio',
        text:<div>
            <p>Al abrir el candado encontrarán dentro de la caja una tarjeta
                      con el mensaje del juego: “Cooperando encontramos la mejor
                      forma de salir” Cada colegio podrá definir el “premio” o dar a
                      elegir a los estudiantes. </p>
                      <br/> 
                      
                      <p><b>Algunas ideas son:</b> </p>
                      <ul style={{padding:'20px'}}>
                      <li>Un desayuno/almuerzo/merienda cooperativa (dar la
                      posibilidad al grupo de organizar un encuentro con espíritu
                      cooperativo en donde se comparta una comida). </li>
                      
                      <li>Organizar una jornada de juegos cooperativos para la escuela,
                      la comunidad o familias, permitiendo que cuenten con un
                      espacio de tiempo semanal para organizar y diseñar esta
                      jornada. 
                      <br/></li>
                      <li>Pintar un mural con una imagen o frase sobre la cooperación. </li>
                      </ul>
              
        </div>
      }
      
    ]
    return(
        <div>
            
            {array.map((item,i)=>(
                <div className="articles example" style={{borderBottom:'solid 1px rgb(245 241 245 / 35%)',marginLeft:'5px',marginRight:'5px',borderRadius:'5px 5px 0 0'}}>            
                <div onClick={() => handleClick(i)} style={{cursor:'pointer',display:'flex', alignItems:'center',width:'100%',justifyContent:'space-between'}}> <h1 >{item.title} </h1>     <div style={{transform:'rotate(90deg)', padding:'20px',height:'100%',width:'auto'}} className={`${clicked === i? 'is-active':''}`} id='arrow' >
                   <span className='line'  style={{ width:'10px',backgroundColor:'#f5f5f7'}} ></span>
                       <span className="line" style={{ width:'10px',backgroundColor:'#f5f5f7'}}></span>
                   </div></div>
            
                 
                <motion.div animate={clicked === i  ? x : y} className={`${clicked ===i ? 'juegoArticleActive':'juegoArticle'}`}   >
                   {item.text}
                 </motion.div>
                         
            
               </div>
            ))}
        </div>
    )
}

export default Fondo2Article