import React from "react";
import '../style2.css'

import PorqueArticle from './porqueArticle'
import PorqueButton from './porqueButton'

export default function Porque (props){
    const handleClick= ()=>
    {
        props.setClicked(!props.clicked)
      
    }
        return (
            <div id={`${props.clicked ? 'porqueActive':'porque'}`}  >
                
            <h1 > Cooperacción tiene  como propósito promover los valores del cooperativismo 
                en la comunidad escolar, dar a conocer sus principales características, 
            sus principios y su historia.<br/>
            <br/>
            <br/></h1>
            
            <div className='width'style={ {borderBottom:'solid 1px rgb(245 241 245 / 35%)', padding:'5px',maxWidth:'700px'}}>
           <div onClick={handleClick} className={'porqueArticleButton'} style={{display:"flex",cursor:'pointer',alignItems:'center'}}> <a style={{textAlign:"start",padding:'0',color:'#f5f1f5',fontSize:'1.5rem',cursor:'pointer',width:'90%'}}  onClick={handleClick}>
                ¿Por qué abordar el tema del cooperativismo con estudiantes en la escuela secundaria?
                
            </a>
            <div style={{transform:'rotate(90deg)', padding:'20px',height:'100%',width:'auto'}} className={`${props.clicked ? 'is-active':''}`} id='arrow' >
            <span className='line'  style={{ width:'10px'}} ></span>
                <span className="line" style={{ width:'10px'}}></span>
            </div>
            </div> 
            <PorqueArticle clicked={props.clicked} />
            </div>
            {/* <PorqueButton clicked={props.clicked} setClicked={props.setClicked} /> */}
            </div>
        )
}

