import React, { useState } from "react";
import { motion } from "framer-motion";
import candado from '../candado.png'


const array = [
    {
        title:'1- ¿Cómo Jugamos?',
        text: <div>
        <p>
          Preguntar a los estudiantes cómo ha sido este juego, en qué se pareció a otros juegos que suelen jugar, en
          qué fue distinto. Escuchar las distintas percepciones y sensaciones; pedir ejemplos que reflejen lo que están
          contando.
          </p>
          <br/>
          <br/>
          <br/>
          <h1 style={{fontWeight: '100'}}>Algunas ideas para reflexionar entre todos:</h1>
          <ul>
              <li>Hay juegos que, como este, no buscan la competencia sino la colaboración. La competencia promueve
              ciertos comportamientos como por ejemplo no compartir información para evitar que el otro gane mientras
              que los juegos cooperativos promueven otro tipo de acciones: compartir nos beneficia a todos,
              involucrarnos en lo que el otro no puede resolver, pedir y brindar ayuda, etc.
              </li>
              <li>
              Trabajar todos juntos implica poner en juego habilidades como la escucha, la búsqueda de acuerdos, el
              intercambio de ideas u opiniones, la resolución de conflictos, etc.
   
              </li>
              <li>
                  
              Todos somos diferentes y eso nos permite pensar que cada uno puede aportar algo distinto al objetivo
              común, es decir, nos complementamos. Pero más allá de eso, todos valemos igual en tanto somos personas
              y formamos parte de ese equipo.
   
              </li>
              <li>
                  
              Cumplir los propios objetivos a la vez que buscar el beneficio de todos es una forma diferente de actuar,
              que puede implicar mayor esfuerzo pero que genera un impacto diferente, que suma valor y que posibilita
              un modo de hacer más sustentable
              </li>
   
          </ul>
          </div>
    },
    {
        title:'2- ¿Qué aprendimos?',
        text: <div>
             <p>
            Sugerimos “mezclar” a todos los estudiantes y armar 4 grupos, de modo tal que no queden tal cual como
jugaron.
            </p>
            <p>Asignarles 10 minutos para que intercambien ideas y resuman en una hoja: <b>“¿qué aprendimos del
cooperativismo?”</b>; pueden también especificar un poco más la consigna indicando que escriban 3 ideas que
les quedaron acerca del cooperativismo:</p>
            <br/>
            <br/>
            <br/>
            
            <ul>
                <li>una sobre qué es el cooperativismo</li>
                <li>otra sobre cómo surgió el cooperativismo</li>
                <li>y la tercera sobre los valores</li>
                <li>Luego de los 10 minutos, hacer una puesta en común, invitando a un representante de cada grupo a contar
lo que resumieron. Después de escuchar a los 3 grupos, puede completar con la información que haya
faltado o que haya quedado poco clara (las tarjetas de ayuda pueden resultar una guía para esto).</li>
                <p>También se puede complementar con otras preguntas como:</p>
                <li>¿Qué conocías o sabías del cooperativismo?</li>
                <li>¿Conocés alguna cooperativa?</li>
                <li>¿Qué te sorprendió o no sabías del cooperativismo?</li>
                <li> ¿Te gustaría profundizar o saber algo más sobre el cooperativismo?</li>

            </ul>
        </div>
    },
    {
        title:'3- ¿Qué nos aporta y en qué podemos aplicarlo?',
        text:
        <div>
             <p>
            Invitar a los estudiantes a pensar sus problemas o situaciones cotidianas
            </p>
            <br/>
            <br/>
            <br/>
            
            <ul>
            <li>¿Qué situaciones del día a día podríamos abordar desde el cooperativismo?</li>
            <li>¿Qué deberíamos dejar de hacer y qué podríamos hacer distinto?</li>
            <li>¿Cómo imaginan un día de escuela en donde todas las personas se comportaran guiados por valores
cooperativos?</li>
            <li>¿Cómo resolverían un problema típico que les toca enfrentar en su vida diaria desde los valores del
cooperativismo?</li>
            </ul>
            <p>A continuación, proponemos algunas ejemplos que podrían utilizarse como “mini casos” para debatir entre
todos qué valores se ven reflejados:</p>
            <ol>
                <li>Un compañero de un año inferior al tuyo necesita ayuda para prepararse para una prueba de
matemática, materia que vos dominás muy bien. Por otro lado, este compañero se ofrece a ayudarte
con el trabajo práctico de arte que estás preparando para la muestra de la semana próxima.
</li>
                <li>
Toda la escuela está preparando un bingo para recaudar fondos para comprar un equipo de sonido
nuevo para los actos escolares ya que el que tenían no funcionaba bien.
</li>
                <li>
Están realizando un trabajo práctico de Historia y un compañero les avisa que no podrá juntarse porque
lo acaban de invitar a un partido de fútbol. Dice que después manda “su parte”.
</li>
                <li>
Todo tu curso organizó un campeonato de voley para que todas las familias del colegio participen. Se
dividieron en grupos, uno tuvo que organizar y preparar el espacio, las canchas, materiales, etc.; otro se
ocupó de la bebida y comida que se vendería durante los partidos y tu grupo se encargó de la difusión e
inscripciones. Al llegar al evento faltan los premios. Ningún grupo siente que era su responsabilidad.
</li>
                <li>
Están organizando el viaje de egresados, con mucho esfuerzo están juntando plata entre todos para
pagar todos los gastos. El viaje se hará en micro excepto una compañera que por su condición física
necesita viajar en avión. El costo de su pasaje será incluido para pagar entre todos.
</li>
                <li>
Un grupo de alumnas y alumnos ha propuesto a la dirección del colegio que los estudiantes puedan
pasar música en los recreos. La directora les dio el permiso. A la hora de seleccionar los temas, este
grupo dice que solo ellos tienen derecho a elegir la música que se pasa ya que han sido quiénes hicieron
la propuesta.
</li>
                <li>
Están organizando un festejo de fin de año entre amigos. Unos quieren hacer un asado y otros
proponen pizza. Deciden votar para definir. Empieza la votación y quien “pone la casa” dice que su voto
vale doble.
</li>
                <li>
Otra escuela de tu comunidad ha sufrido destrozos en algunas aulas producto de un fuerte temporal.
Tu escuela ofrece aulas para que los cursos afectados no pierdan días de clases hasta que se pueda
reparar todo.
</li>
                <li>
La escuela les propone trabajar en un comité de convivencia que estará formado por estudiantes y
profesores. Se tomarán decisiones dando lugar al debate primero y luego a una votación, en donde cada
persona tiene 1 voto y se gana por mayoría.
</li>
                <li>
Una familia de la escuela está atravesando una situación complicada y un grupo de alumnos y
alumnas del colegio deciden organizar una rifa para recaudar fondos que serán entregados a dicha
familia.</li>
            </ol>
        </div>
    },
    {
        title:'4- Cerrar para abrir',
        text:   <div  >
        {/* <h3 className="title"   > CERRAR PARA ABRIR</h3> */}
        
       
        <p>
        Poder dar un cierre a la actividad no debe implicar el cierre del
        tema. <br/>Es interesante poder detectar si ha despertado nuevas preguntas,
        si han relacionado esto con temas o contenidos que están
        viendo en otras materias, si tienen curiosidad por saber más, etc. <br/>
        Proponemos entonces finalizar la actividad resumiendo las ideas
        centrales que quedaron y las preguntas que podrían seguir trabajándose.
        </p>
        <div style={{display:'flex', padding:'30px'}}>
        <img src={candado} style={{width:'60px',height:'auto', paddingTop:'30px'}} />
        </div>
    </div>
    }
    
]
export default function Fondo3Article (props){
    const [clicked,setClicked] = useState (null)
   
    const x = {opacity:1, y : 0,
    }
  const y = {opacity:0,y: 0, 
    
  }


    const handleClick =(i)=>{
        if (clicked === i){
            return setClicked(null)
        }
        setClicked (i)
    }

   
      
    return(
        <div>
            
            {array.map((item,i)=>(
                <div className="articles example" style={{borderBottom:'solid 1px rgb(245 241 245 / 35%)',marginLeft:'5px',marginRight:'5px',borderRadius:'5px 5px 0 0'}}>            
                <div onClick={() => handleClick(i)} style={{cursor:'pointer',display:'flex', alignItems:'center',width:'100%',justifyContent:'space-between'}}> <h1 >{item.title} </h1>     <div style={{transform:'rotate(90deg)', padding:'20px',height:'100%',width:'auto'}} className={`${clicked === i? 'is-active':''}`} id='arrow' >
                   <span className='line'  style={{ width:'10px',backgroundColor:'#f5f5f7'}} ></span>
                       <span className="line" style={{ width:'10px',backgroundColor:'#f5f5f7'}}></span>
                   </div></div>
            
                 
                <motion.div animate={clicked === i  ? x : y} className={`${clicked ===i ? 'juegoArticleActive':'juegoArticle'}`}   >
                   {item.text}
                 </motion.div>
                         
            
               </div>
            ))}
        </div>
    )
}